import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import CryptoTop100 from '@/components/CryptoTop100'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/derp',
      name: 'CryptoTop100',
      component: CryptoTop100
    }
  ]
})



