<template>
    <div>
        <h1>{{ title }}</h1>
        <table>
            <tr>
                <th>Rank</th>
                <th>Symbol</th>
                <th>Name</th>
                <th>Price (USD)</th>
                <th>Market Cap</th>
                <th>Supply (Available/Total/Max)</th>
                <th>Percent Change (1h/24h/7d)</th>
            </tr>
            <tr v-for="crypto in cryptos">
                <td>{{ crypto.rank }}</td>
                <td>{{ crypto.symbol }}</td>
                <td>{{ crypto.name }}</td>
                <td>{{ crypto.price_usd }}</td>
                <td>{{ crypto.market_cap_usd }}</td>
                <td>{{ crypto.available_supply }} {{ crypto.total_supply }} {{ crypto.max_supply }}</td>
                <td>{{ crypto.percent_change_1h }} {{ crypto.percent_change_24h }} {{ crypto.percent_change_7d }}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    data: function () {
        return {
            title: 'Top 100 Cryptocurrencies',
            cryptos: [],
            timer: null
        }
    },
    props: ['cryptos'],
    created: function () {
        // Fetch all the cryptos!
        this.fetchCryptos();
        // Then fetch them every 30 seconds
        this.timer = setInterval(() => {
            this.fetchCryptos();
        }, 120000);
    },
    methods: {
        fetchCryptos: function() {
            fetch('https://api.coinmarketcap.com/v1/ticker/?limit=10').then(response => {
                return response.json();
            }).then(data => {
                this.cryptos = data;
            });
        }
    }
}
</script>


